.section-header {
  display: flex;
  flex: 0 1;
  align-items: center;
  justify-content: center;
  max-height: 150px;
  background-color: #c7dcff;
  padding: 22px 0 22px 0;
  /* border-top: 1px solid #6b8eff; */
}

.section-header-content {
  display: flex;
  flex: 1;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  gap: 16px;
}
.section-header-content .section-header-icon {
  height: 24px;
  padding-bottom: 8px;
}

.section-header-text {
  display: flex;
  flex: 1;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: flex-end;
  gap: 12px;
}
.section-header-text > h3 {
  font: normal normal normal 22px/22px Larsseit;
}
.section-header-text > span {
  text-align: left;
  font: normal normal normal 16px/20px Larsseit;
  letter-spacing: 0px;
  color: #222222b3;
  opacity: 1;
}

.section-header-action-icon {
  height: 24px;
  cursor: pointer;
  fill: black;
}